<template>
  <div v-loading="isLoading">
    <fm-generate-form class="form-box" :data="jsonData" v-if='isShowForm' ref="generateForm">
    </fm-generate-form>

    <section class="export-btn">
      <el-button type="primary" size="default" @click="handleOutPut">
        导出txt
      </el-button>
    </section>

  </div>
</template>

<script>
  export default {
    name: '',
    data() {
      return {
        detail: {},
        jsonData: {},
        isShowForm: false,
        detailid: 0,
        isLoading: false,
      }
    },
    watch: {
      '$route'(value) {
        value && this.getFormDetail()
      }
    },
    created() {
      // this.handleOutPut();
      this.getFormDetail();

    },
    methods: {
      async getFormDetail() {
        this.isLoading = true;
        let resdata = await this.$http.post('/form/getFormDetail', {
          id: this.$route.query.id,
        }).catch(d => this.isLoading = false)
        this.isLoading = false;
        if (!resdata || !resdata.data) return
        this.detail = resdata.data;
        let json = decodeURIComponent(this.detail.formdata)
        this.jsonData = JSON.parse(json)
        this.$nextTick(() => {
          this.isShowForm = true;
          this.isLoading = false;
        })
      },
      getTime() {

      },
      handleOutPut() {
        this.$refs.generateForm.getData().then((data) => {
          let outData = [];
          Object.keys(data).forEach(key => {
            if (!key.includes('demo') && !key.includes('date_pubish')) {
              //数组用、隔开
              let value = data[key] instanceof Array ? data[key].join('、') : data[key];
              
              let name = this.jsonData.list.find(item => item.model == key).name;
              value = value.replace(/\(前端\)/g, '').replace(/\(后端\)/g, '')
              outData.push(name + ':' + value + '\t\n');
            }
          })

          outData = outData.map((item, index) => {
            item = (index + 1) + '.' + item;
            return item;
          })
          outData.unshift(data['date_updatetime'] + `-网厅-正式环境更新说明：` + '\t\n')


          const element = document.createElement('a');
          element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(outData.join('')));
          element.setAttribute('download', data['date_pubish'])
          element.style.display = 'none'
          element.click()
        }).catch(e => {})
      }
    }
  }
</script>

<style lang="less" scoped>
  .form-box {

    // height: 600px;
    overflow-y: scroll;
    margin: 50px auto;
    padding: 20px 40px;
    margin-bottom: 100px;
    width: 80%;
    border: 1px solid #eee;

  }

  .export-btn {
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 0;
    z-index: 9;
    left: 0;
    background: #fff;
    border-top: 1px solid #eee;

    .txt {}
  }
</style>


